[class*="desktop.grid"] {
    display: flex;
    flex-wrap: wrap;
}

[class*="desktop.grid(1"] {
    display: block;

    > * {
        width: 100%;
    }
}

[class*="desktop.grid"] > :last-child {
    margin-bottom: 0;
}

[class*="desktop.grid(2"],
[class*="desktop.grid(3"],
[class*="desktop.grid(4"],
[class*="desktop.grid(5"],
[class*="desktop.grid(6"] {
    > :nth-last-child(2) {
        margin-bottom: 0;
    }
}

[class*="desktop.grid(3"],
[class*="desktop.grid(4"],
[class*="desktop.grid(5"],
[class*="desktop.grid(6"] {
    > :nth-last-child(3) {
        margin-bottom: 0;
    }
}

[class*="desktop.grid(4"],
[class*="desktop.grid(5"],
[class*="desktop.grid(6"] {
    > :nth-last-child(4) {
        margin-bottom: 0;
    }
}

[class*="desktop.grid(5"],
[class*="desktop.grid(6"] {
    > :nth-last-child(5) {
        margin-bottom: 0;
    }
}

[class*="desktop.grid(6"] {
    > :nth-last-child(6) {
        margin-bottom: 0;
    }
}

@for $cols from 1 through 6 {
    $colSize: percentage(1) / $cols;

    .desktop\.grid\(#{$cols}\,0\,0\) {
        > * {
            width: $colSize;
        }
    }
}

@for $rowGap from 1 through 60 {
    $rgap: $rowGap * 5;

    .desktop\.grid\(1\,0\,#{$rgap}\) {
        > * {
            margin-bottom: #{$rgap}px;
        }
    }
}

@for $cols from 2 through 6 {
    [class*="desktop.grid(#{$cols}"] {
        > :nth-child(#{$cols}n) {
            margin-right: 0;
        }
    }

    @for $colGap from 0 through 10 {
        @for $rowGap from 0 through 10 {
            @if $colGap == 0 and $rowGap == 0 {}
            @else {
                $cgap: $colGap * 5;
                $rgap: $rowGap * 5;
                $colSize: percentage(1) / $cols;
                $colSizeAdjust: #{$cgap - $cgap / $cols}px;

                .desktop\.grid\(#{$cols}\,#{$cgap}\,#{$rgap}\) {
                    > * {
                        width: calc(#{$colSize} - #{$colSizeAdjust});
                        margin-right: #{$cgap}px;
                        margin-bottom: #{$rgap}px;
                    }
                }
            }
        }
    }
}
